@use './common/variables' as v;
@use './common/mixin' as m;

@use '../components/global/header';
@use '../components/global/footer';
@use '../components/global/font';
@use '../components/global/ui';
@use '../components/global/animation';
@use '../components/contact/contact';

body {
    @include m.sp {
        padding-top: 75px;
    }
}

.main {
    background-color: v.$colorWhite;
    position: relative;
    z-index: 2;
}

.h-screen-sp {
    @include m.sp {
        min-height: calc(100vh - 75px);
    }
}
